import React, { Component } from 'react'
import { ForecastList } from './components/ForecastList';
import Header from './components/Header';
import Footer from  './components/Footer';
import axios from 'axios';
import './App.css';

export default class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
        weatherData: [],
        isFetched: false,
        validStartPeriod: "",
        validEndPeriod: "",
        lastUpdated: "",
    };
  }

  componentDidMount() {
    axios.get('https://api.data.gov.sg/v1/environment/2-hour-weather-forecast')
    .then(resp => {
      console.log(resp.data);
      this.setState({
        weatherData: resp.data.items[0].forecasts, 
        validStartPeriod: new Date(resp.data.items[0].valid_period.start),
        validEndPeriod: new Date(resp.data.items[0].valid_period.end),
        lastUpdated: new Date(resp.data.items[0].update_timestamp),
        isFetched: true
      });
    })
    .catch(error => console.error(error))
  }

  render() {
    return (
      <div className="container">
        <Header />
        {this.state.isFetched ? <ForecastList forecasts={this.state.weatherData} forecastStart={this.state.validStartPeriod} forecastEnd={this.state.validEndPeriod} /> : "Fetching weather forecasts..."}
        <Footer lastUpdated={this.state.lastUpdated} />
      </div>
    )
  }
}
