import React from 'react'
import Forecast from './Forecast'
import { ForecastPeriod } from './ForecastPeriod'

export const ForecastList = ({ forecasts, forecastStart, forecastEnd }) => {
    return (
        <div>
            <ForecastPeriod forecastStart={forecastStart} forecastEnd={forecastEnd} />
            {forecasts.map(forecast => <Forecast key={forecast.area} forecast={forecast} />)}
        </div>
    )
}
