import React from 'react'

const Header = ({ title }) => {
    return (
        <header className="header">
            <h1>{title}</h1>
            {/* <a href="https://data.gov.sg/">Source: data.gov.sg</a> */}
        </header>
    )
}

Header.defaultProps = {
    title: '2 Hour Weather Forecast Singapore'
}

export default Header
